import React from "react";
import logo from "./logo.svg"
const footer=()=>{
    return(<>
        <footer>
        <div id="left">
        <div className="smallcards">
        <img src={logo} alt="logo"/>
        <div id="contact"><li><img src="images/icon-facebook.svg" alt=""/>
        </li>
        <li><img src="images/icon-youtube.svg" alt=""/>
        </li>
        <li><img src="images/icon-twitter.svg" alt=""/>
        </li>
        <li><img src="images/icon-pinterest.svg" alt=""/>
        </li>
        <li><img src="images/icon-instagram.svg" alt=""/>
        </li></div>
        </div>
        <div className="smallcards">
        <a href="#"> About Us</a>
        <a href="#">Contact</a>
        <a href="#">Blog</a>
        </div>
        <div className="smallcards">
        <a href="#">Careers</a>
        <a href="#">Support</a>
        <a href="#">Privacy Policy</a>
        </div>

        </div>
        <div id="right">
        <div className="btn">Request Invite</div>
        <div id="cr">© Easybank. All Rights Reserved</div>
        </div>
        </footer>
        </>)
}
export default footer;